import React from 'react';
import {HorizontalBar} from 'react-chartjs-2';
import Colores from '../../assets/colores';

function ChartHorizontalBar(props) {
    const data = {
        labels: props.labels,
        datasets: [
          {
            label: props.label,
            backgroundColor: Colores[props.color].backGround,
            borderColor: Colores[props.color].border,
            borderWidth: 1,
            hoverBackgroundColor: Colores[props.color].border,
            hoverBorderColor: Colores[props.color].hoverBorder,
            data: props.data
          }
        ]
      };

    return (
        <HorizontalBar data={data} />
    );
}

export default ChartHorizontalBar;