import React from 'react';
import { Row } from 'rsuite';
import Element from './Element';
import { AreaChart, Area } from 'recharts';
import IconNew from '../../assets/icons/new.png';
import IconRegistros from '../../assets/icons/registros.png';

function RowTop(props){
    const { cargarlength, length, cargarCreatedOn, createdOn, onClick } = props;
    return(
        <Row className="box-element-container">
            { cargarlength ?
                <Element style={{"background":"#006666"}} md={8} fill={1}>
                    <div className="box-element-top-content">
                        <div className="box-element-top-icon">
                            <img src={IconRegistros} alt="Icono registros" />
                        </div>
                        <div className="box-element-top-text">
                            <span>{length}</span>
                            <span>Registros totales</span>
                        </div>
                    </div>
                </Element>
                :<Element md={8} />
            }
            { cargarCreatedOn ?
                <Element color={1} title="Actividad" style={{"background":"#339966"}} md={8} fill={1}>
                    <AreaChart className="responsive-chart" width={700}  height={100} data={createdOn}
                        margin={{ top: 10, right: 10, left: 10, bottom: 0 }}>
                        <defs>
                        <linearGradient id="colorChartDatesActivity" x1="0" y1="0" x2="0" y2="1">
                            <stop offset="0%" stopColor="#d9f2e5" stopOpacity={0.8}/>
                            <stop offset="90%" stopColor="#40bf7d" stopOpacity={0.8}/>
                        </linearGradient>
                        </defs>
                        <Area type="monotone" dataKey="value" stroke="#d9f2e5" fillOpacity={1} fill="url(#colorChartDatesActivity)" />
                    </AreaChart>
                </Element>
                :<Element md={8} />
            }
            { cargarCreatedOn ?
                <Element style={{"background":"#339933"}} md={8} fill={1}>
                    <div  onClick={onClick} className="box-element-top-content">
                        <div className="box-element-top-icon">
                            <img src={IconNew} alt="Icono nuevos registros" />
                        </div>
                        <div className="box-element-top-text">
                            <span>{createdOn[createdOn.length -1].value}</span>
                            <span>Registros acumulados {createdOn[createdOn.length -1].name}</span>
                        </div>
                    </div>
                </Element>
                :<Element md={8} />
            }
        </Row>
    );
}

export default RowTop;