import React from 'react';
import {Pie } from 'react-chartjs-2';

function ChartPie(props) {
		const data = {
			labels: props.labels,
			datasets: [{
				data: props.data,
				backgroundColor: [
				'#FF6384',
				'#36A2EB',
				'#FFCE56',
				'#009999',
				'#ff0000'
				],
				hoverBackgroundColor: [
				'#FF6384',
				'#36A2EB',
				'#FFCE56',
				'#009999',
				'#ff0000'
				]
			}]
		};

        return (
        <Pie data={data} />
        );
}

export default ChartPie;