import React, { Component } from 'react';
import { Grid, Row, Progress } from 'rsuite';
import URL_API from  '../../assets/conf';
import Element from '../elements/Element';
import RowTop from '../elements/RowTop';
import GetDate from '../../assets/dates';

import ChartBar from '../charts/Bar';
import ChartPie from '../charts/Pie';
import ChartHorizontalBar from '../charts/HorizontalBar';

const { Circle } = Progress;

class P1Madres extends Component{
    state = {
        length: 0,
        cargarLength: false,
        createdOn: {},
        cargarCreatedOn: false,
        grupo: {},
        cargarGrupo: false,

        entidad: {},
        cargarEntidad: false,
        areaUnidad: {},
        cargarAreaUnidad: false,
        beneficiarioINSABI: {},
        cargarBeneficiarioINSABI: false,
        clinica: {},
        cargarClinica: false,

        edad: {},
        cargarEdad: false,
        estaEmbarazada: {},
        cargarEstaEmbarazada: false,
        sexoHijo: {},
        cargarSexoHijo: false,
        edadHijo: {},
        cargarEdadHijo: false,

        parentesco: {},
        cargarParentesco: false,
        numPersonasHogar: {},
        cargarNumPersonasHogar: false,
        posicionHijo: {},
        cargarPosicionHijo: false,
        numMenores: {},
        cargarNumMenores: false,
        estadoCilvil: {},
        cargarEstadoCilvil: false,
        escolaridad: {},
        cargarEscolaridad: false,
        dedicacion: {},
        cargarDedicacion: false,
        programaSocial: {},
        cargarProgramaSocial: false,
    }

    fetchDataAll = async () =>{
        try {
            let resp = await fetch(`${URL_API}/madres`);
            let data = await resp.json();

            if (data.msg === "successful")
            {
                data = data.data;

                return {
                    length: data.length,
                    createdOn: data.map(e => { let date = new Date(e.fe_o);return `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}` }),
                    grupo: data.map(e => { return parseInt(e.cc_1_4) }),
                    
                    entidad: data.map(e =>{  return parseInt(e.ig_1_1)  }),
                    areaUnidad: data.map(e =>{  return parseInt(e.ig_1_4)  }),
                    beneficiarioINSABI: data.map(e =>{  return parseInt(e.ig_1_5)  }),
                    clinica: data.map(e =>{  return e.cc_1_1  }),

                    edad: data.map(e =>{  return parseInt(e.dge_1_1)  }),
                    estaEmbarazada: data.map(e =>{  return parseInt(e.dge_1_2)  }),
                    sexoHijo: data.map(e =>{  return parseInt(e.dge_1_4)  }),
                    edadHijo: data.map(e =>{  return parseInt(e.dge_e_ninx)  }),

                    parentesco: data.map(e => { return parseInt(e.dge_1_6) }),
                    posicionHijo: data.map(e => { return parseInt(e.dge_1_7) }),
                    numPersonasHogar: data.map(e => { return parseInt(e.dge_1_8) }),
                    numMenores: data.map(e => { return parseInt(e.dge_1_9) }),
                    estadoCilvil: data.map(e => { return parseInt(e.dge_1_10) }),
                    escolaridad: data.map(e => { return parseInt(e.dge_1_11) }),
                    dedicacion: data.map(e => { return parseInt(e.dge_1_12) }),
                    programaSocial: data.map(e => { return parseInt(e.dge_1_15) }),
                }
            }
        } catch (error) {
            return "error"
        }
    }

    getDataChartCreatedOn = async (data) =>{
        let obj = new GetDate();
        let labels = obj.getDates();
        let values = await obj.getValuesDates(labels, data);

        this.setState({
            createdOn: values,
            cargarCreatedOn: true
        });
    }

    getDataChartGrupo = async (data) =>{
        this.setState({
            grupo: {
                control: data.filter(e => e === 1).length,
                intervencion: data.filter(e => e === 2).length,
            },
            cargarGrupo: true
        });
    }

    getDataChartEntidad = async (data) =>{
        let porcentaje = 100 / data.length;

        this.setState({
            entidad: {
                morelos: ((data.filter(e => e === 17).length) * porcentaje).toFixed(2),
                yucatan: ((data.filter(e => e === 31).length) * porcentaje).toFixed(2),
            },
            cargarEntidad: true,
        });
    }

    getDataChartAreaUnidad = async (data) =>{
        this.setState({
            areaUnidad:{
                urbano: data.filter(e => e === 2).length,
                rural: data.filter(e => e === 1).length,
            },
            cargarAreaUnidad: true,
        });
    }

    getDataChartBeneficiarioINSABI = async (data) =>{
        this.setState({
            beneficiarioINSABI: {
                _1_ : data.filter(e => e === 1).length,
                _2_ : data.filter(e => e === 2).length,
                _99_ : data.filter(e => e === 99).length,
                nan : data.filter(e => e !== 1 && e !== 2 && e !== 99).length,
            },
            cargarBeneficiarioINSABI: true,
        });
    }

    getDataChartClinica = async (data) =>{
        let array = [];
        let dataTemp = [];
        let dataValue = [];

        data.forEach(e => {
            if (!dataTemp.includes(e)){
                dataTemp.push(e)
            }
        });

        dataValue = dataTemp.map(e => {
            return data.filter(ee => e === ee).length;
        });

        for (let i = 0; i <= dataTemp.length - 1; i++){
            array.push({
                "n": i + 1,
                "clave": dataTemp[i],
                "registros": dataValue[i]
            });
        }
        
        this.props.setDataPanel2(array);
    }

    getDataChartEdad = async (data) =>{
        this.setState({
            edad: {
                _1_: data.filter(e => e >= 11 && e <= 20).length,
                _2_: data.filter(e => e >= 21 && e <= 30).length,
                _3_: data.filter(e => e >= 31 && e <= 40).length,
                _4_: data.filter(e => e >= 41 && e <= 50).length,
                _5_: data.filter(e => e >= 51).length,
                nan: data.filter(e => isNaN(e)).length,
            },
            cargarEdad: true
        });
    }

    getDataChartEstaEmbarazada = async (data) =>{
        this.setState({
            estaEmbarazada: {
                _1_: data.filter(e => e === 1).length,
                _2_: data.filter(e => e === 2).length,
                _8666_: data.filter(e => e === 8666).length,
                nan: data.filter(e => isNaN(e)).length,
            },
            cargarEstaEmbarazada: true,
        });
    }

    getDataChartSexoHijo = async (data) =>{
        this.setState({
            sexoHijo: {
                _1_: data.filter(e => e === 1).length,
                _2_: data.filter(e => e === 2).length,
                _3_: data.filter(e => e === 9).length,
                nan: data.filter(e => isNaN(e)).length,
            },
            cargarSexoHijo: true,
        });
    }

    getDataChartEdadHijo = async (data) =>{
        this.setState({
            edadHijo: {
                _1_: data.filter(e => e >= 0 && e <= 10).length,
                _2_: data.filter(e => e >= 11 && e <= 20).length,
                _3_: data.filter(e => e >= 21 && e <= 30).length,
                _4_: data.filter(e => e >= 31 && e <= 40).length,
                _5_: data.filter(e => e >= 41).length,
                nan: data.filter(e => isNaN(e)).length,
            },
            cargarEdadHijo: true,
        });
    }

    getDataChartParentesco = async (data) =>{
        this.setState({
            parentesco: {
                _1_: data.filter(e => e === 1).length,
                _2_: data.filter(e => e === 2).length,
                _3_: data.filter(e => e === 3).length,
                _4_: data.filter(e => e === 4).length,
                _5_: data.filter(e => e === 5).length,
                _6_: data.filter(e => e === 6).length,
                _1111_: data.filter(e => e === 1111).length,
                _8666_: data.filter(e => e === 8666).length,
                nan: data.filter(e => e !==1 && e !==2 && e !==3 && e !==4 && e !==5 && e !==6 && e !==1111 && e !==8666).length,
            },
            cargarParentesco: true
        });
    }

    getDataChartPosicionHijo = async (data) =>{
        this.setState({
            posicionHijo: {
                _1_: data.filter(e => e === 1 || e === 0).length,
                _2_: data.filter(e => e === 2).length,
                _3_: data.filter(e => e === 3).length,
                _4_: data.filter(e => e >= 4).length,
                nan: data.filter(e => isNaN(e)).length,
            },
            cargarPosicionHijo: true,
        });
    }
    
    getDataChartNumPersonasHogar = async (data) =>{
        this.setState({
            numPersonasHogar: {
                _1_: data.filter(e => e === 1 || e === 2).length,
                _2_: data.filter(e => e === 3 || e === 4).length,
                _3_: data.filter(e => e === 5 || e === 6).length,
                _4_: data.filter(e => e >= 7).length,
                nan: data.filter(e => isNaN(e)).length,
            },
            cargarNumPersonasHogar: true,
        });
    }
    
    getDataChartNumMenores = async (data) =>{
        this.setState({
            numMenores: {
                _1_: data.filter(e => e === 0).length,
                _2_: data.filter(e => e === 1).length,
                _3_: data.filter(e => e === 2).length,
                _4_: data.filter(e => e >= 3).length,
                nan: data.filter(e => isNaN(e)).length,
            },
            cargarNumMenores: true,
        });
    }
    
    getDataChartEstadoCilvil = async (data) =>{
        this.setState({
            estadoCilvil: {
                _1_: data.filter(e => e === 1).length,
                _2_: data.filter(e => e === 2).length,
                _3_: data.filter(e => e === 3).length,
                _4_: data.filter(e => e === 4).length,
                _5_: data.filter(e => e === 5).length,
                _6_: data.filter(e => e === 6).length,
                nan: data.filter(e => e !==1 && e !==2 && e !==3 && e !==4 && e !==5 && e !==6).length,
            },
            cargarEstadoCilvil: true
        });
    }
    
    getDataChartEscolaridad = async (data) =>{
        this.setState({
            escolaridad: {
                _1_: data.filter(e => e === 1).length,
                _2_: data.filter(e => e === 2).length,
                _3_: data.filter(e => e === 3).length,
                _4_: data.filter(e => e === 4).length,
                _5_: data.filter(e => e === 5).length,
                _6_: data.filter(e => e === 6).length,
                _7_: data.filter(e => e === 7).length,
                _8_: data.filter(e => e === 8).length,
                _8555_: data.filter(e => e === 8555).length,
                _8666_: data.filter(e => e === 8666).length,
                nan: data.filter(e => e !==1 && e !==2 && e !==3 && e !==4 && e !==5 && e !==6 && e !==7  && e !==8  && e !==8555 && e !==8666).length,
            },
            cargarEscolaridad: true
        });
    }

    getDataChartDedicacion = async (data) =>{
        this.setState({
            dedicacion: {
                _1_: data.filter(e => e === 1).length,
                _2_: data.filter(e => e === 2).length,
                _3_: data.filter(e => e === 3).length,
                _1111_: data.filter(e => e === 1111).length,
                nan: data.filter(e => e !==1 && e !==2 && e !==3 && e !==1111).length,
            },
            cargarDedicacion: true
        });
    }
    
    getDataChartProgramaSocial = async (data) =>{
        this.setState({
            programaSocial: {
                _1_: data.filter(e => e === 1).length,
                _2_: data.filter(e => e === 2).length,
                _8666_: data.filter(e => e === 8666).length,
                nan: data.filter(e => e !==1 && e !==2 && e !==8666).length,
            },
            cargarProgramaSocial: true
        });
    }

    async componentDidMount(){
        let data = await this.fetchDataAll();

        if (data !== "error")
        {
            this.setState({
                length: data.length,
                cargarlength: true
            });

            await this.getDataChartCreatedOn(data.createdOn);
            await this.getDataChartGrupo(data.grupo);
            
            await this.getDataChartEntidad(data.entidad);
            await this.getDataChartAreaUnidad(data.areaUnidad);
            await this.getDataChartBeneficiarioINSABI(data.beneficiarioINSABI);
            await this.getDataChartEdad(data.edad);
            await this.getDataChartEstaEmbarazada(data.estaEmbarazada);
            await this.getDataChartSexoHijo(data.sexoHijo);
            await this.getDataChartEdadHijo(data.edadHijo);

            await this.getDataChartParentesco(data.parentesco);
            await this.getDataChartNumPersonasHogar(data.numPersonasHogar);
            await this.getDataChartPosicionHijo(data.posicionHijo);
            await this.getDataChartNumMenores(data.numMenores);
            await this.getDataChartEstadoCilvil(data.estadoCilvil);
            await this.getDataChartEscolaridad(data.escolaridad);
            await this.getDataChartDedicacion(data.dedicacion);
            await this.getDataChartProgramaSocial(data.programaSocial);

            await this.getDataChartClinica(data.clinica);
            
        }
    }

    prueba(){
        alert("Moco");
    }

    render(){
        return(
            <Grid fluid style={{margin:0, padding:0}}>
                {/* Actividades generales */}
                <RowTop
                    cargarlength={this.state.cargarlength}
                    length={this.state.length}
                    cargarCreatedOn={this.state.cargarCreatedOn}
                    createdOn={this.state.createdOn}
                />

                {/* Porcentajes morelos/yucatan */}
                <Row className="box-element-container">
                    { this.state.cargarEntidad ?
                        <Element title="Registros Morelos" md={12} fill={1}>
                            <Circle  style={{width: "30%", margin:10}} percent={parseFloat(this.state.entidad.morelos)} strokeColor="#ffc107" />
                        </Element>
                        : <Element md={12} />
                    }
                    { this.state.cargarEntidad ?
                        <Element title="Registros Yucatan" md={12} fill={1}>
                            <Circle style={{width: "30%", margin:10}} percent={parseFloat(this.state.entidad.yucatan)} strokeColor="#b3b3ff" />
                        </Element>
                        : <Element md={12} />
                    }
                </Row>

                {/* Graficas de grupo, Area unidad y Beneficiario insabi*/}
                <Row className="box-element-container">
                    { this.state.cargarGrupo ?
                        <Element title="Grupo" md={8} fill={1}>
                            <ChartPie
                                data={[this.state.grupo.control, this.state.grupo.intervencion]} 
                                labels={["Control", "Intervención"]}
                            />
                        </Element>
                        :<Element md={8}/>
                    }
                    { this.state.cargarAreaUnidad ?
                        <Element title="Área de unidad" md={8} fill={1}>
                            <ChartPie
                                labels={["Rural", "Urbana"]} 
                                data={[this.state.areaUnidad.rural, this.state.areaUnidad.urbano]}
                            />
                        </Element>
                        : <Element md={8} />
                    }
                    { this.state.cargarBeneficiarioINSABI ?
                        <Element title="Beneficiari@s INSABI" md={8} fill={1}>
                            <ChartPie
                                labels={["Si", "No", "No sabe", "Sin responder"]} 
                                data={[this.state.beneficiarioINSABI._1_, this.state.beneficiarioINSABI._2_, this.state.beneficiarioINSABI._99_, this.state.beneficiarioINSABI.nan]}
                            />
                        </Element>
                        : <Element md={8} />
                    }
                </Row>
                
                {/* Graficas de Edad y Esta embarazada */}
                <Row className="box-element-container">
                    { this.state.cargarEdad ?
                        <Element title="Frecuencia de edades (Cuidadora)" md={12} fill={1}>
                            <ChartPie
                                labels={["11-20 años", "21-30 años", "31-40 años", "41-50 años", "+50 años", "Sin responder"]}
                                data={[this.state.edad._1_, this.state.edad._2_, this.state.edad._3_, this.state.edad._4_, this.state.edad._5_, this.state.edad.nan]}
                            />
                        </Element>
                        : <Element md={12} />
                    }
                    { this.state.cargarEstaEmbarazada ?
                        <Element title="¿Actualmente esta embarazada?" md={12} fill={1}>
                            <ChartBar color={0} label="Registros"
                                data={[this.state.estaEmbarazada._1_, this.state.estaEmbarazada._2_, this.state.estaEmbarazada._8666_, this.state.estaEmbarazada.nan]} 
                                labels={["Si", "No", "No sabe", "Sin responder"]}
                            />
                        </Element>
                        :<Element md={12}/>
                    }
                </Row>

                {/* Graficas de Edad hijo y sexo hijo */}
                <Row className="box-element-container">
                    { this.state.cargarSexoHijo ?
                        <Element title="Sexo hij@" md={12} fill={1}>
                            <ChartBar color={0} label="Registros"
                                data={[this.state.sexoHijo._1_, this.state.sexoHijo._2_, this.state.sexoHijo._3_, this.state.sexoHijo.nan]} 
                                labels={["Hombre", "Mujer", "Embarazada", "Sin responder"]}
                            />
                        </Element>
                        :<Element md={12}/>
                    }
                    { this.state.cargarEdadHijo ?
                        <Element title="Frecuencia de edades (Hij@)" md={12} fill={1}>
                            <ChartPie
                                labels={["0-10 meses", "11-20 meses", "21-30 meses", "31-40 meses", "+40 meses", "Sin responder"]}
                                data={[this.state.edadHijo._1_, this.state.edadHijo._2_, this.state.edadHijo._3_, this.state.edadHijo._4_, this.state.edadHijo._5_, this.state.edadHijo.nan]}
                            />
                        </Element>
                        : <Element md={12} />
                    }
                </Row>

                {/* Graficas de parentesco/Numero de personas en el hogar */}
                <Row className="box-element-container">
                    { this.state.cargarParentesco ?
                        <Element title="Parentesco niñ@" md={12} fill={1}>
                            <ChartHorizontalBar 
                                color={1}
                                label="Registros"
                                labels={["Madre", "Abuela", "Tia/Hermana", "Padre/Abuelo", "Tio/Hermano", "Ningún parentesco", "Otro parentesco", "No sabe", "Sin responder"]} 
                                data={[this.state.parentesco._1_, this.state.parentesco._2_, this.state.parentesco._3_, this.state.parentesco._4_, this.state.parentesco._5_, this.state.parentesco._6_, this.state.parentesco._1111_, this.state.parentesco._8666_ , this.state.parentesco.nan]}
                            />
                        </Element>
                        : <Element md={12} />
                    }
                    { this.state.cargarNumPersonasHogar ?
                        <Element title="Número de personas en el hogar" md={12} fill={1}>
                            <ChartBar 
                                color={2} 
                                label="Registros" 
                                labels={["1-2 Personas", "3-4 Personas", "5-6 Personas", "+7 Personas", "Sin responder"]}
                                data={[this.state.numPersonasHogar._1_, this.state.numPersonasHogar._2_, this.state.numPersonasHogar._3_, this.state.numPersonasHogar._4_, this.state.numPersonasHogar.nan]} 
                            />
                        </Element>
                        : <Element md={12} />
                    }
                </Row>

                {/* Graficas de Posicion hijo/Numero de menores */}
                <Row className="box-element-container">
                    { this.state.cargarPosicionHijo ?
                        <Element title="Posición niñ@" md={12} fill={1}>
                            <ChartBar 
                                color={3}
                                label="Registros"
                                labels={["P. 1", "P. 2", "P. 3", "P. +4", "Sin responder"]} 
                                data={[this.state.posicionHijo._1_, this.state.posicionHijo._2_, this.state.posicionHijo._3_, this.state.posicionHijo._4_, this.state.posicionHijo.nan]}
                            />
                        </Element>
                        : <Element md={12} />
                    }
                    { this.state.cargarNumMenores ?
                        <Element title="Menores de 5 años (hogar)" md={12} fill={1}>
                            <ChartBar 
                                color={4} 
                                label="Registros" 
                                labels={["0 M.", "1 M.", "2 M.", "+3 M.", "Sin responder"]}
                                data={[this.state.numMenores._1_, this.state.numMenores._2_, this.state.numMenores._3_, this.state.numMenores._4_, this.state.numMenores.nan]} 
                            />
                        </Element>
                        : <Element md={12} />
                    }
                </Row>
                 
                 {/* Graficas de Estado civil/Escolaridad */}
                <Row className="box-element-container">
                    { this.state.cargarEstadoCilvil ?
                        <Element title="Estado civil" md={12} fill={1}>
                            <ChartHorizontalBar
                                color={5}
                                label="Registros"
                                labels={["Soltera", "Casada", "Vive en unión libre", "Separada", "Divorciada", "Viuda", "Sin responder"]} 
                                data={[this.state.estadoCilvil._1_, this.state.estadoCilvil._2_, this.state.estadoCilvil._3_, this.state.estadoCilvil._4_, this.state.estadoCilvil._5_, this.state.estadoCilvil._6_, this.state.estadoCilvil.nan]}
                            />
                        </Element>
                        : <Element md={12} />
                    }
                    { this.state.cargarEscolaridad ?
                        <Element title="Escolaridad" md={12} fill={1}>
                            <ChartHorizontalBar 
                                color={0} 
                                label="Registros" 
                                labels={[
                                    "Preescolar/Kinder", 
                                    "Primaria", 
                                    "Secundaria", 
                                    "Preparatoria/Bachillerato",
                                    "Normal básica", 
                                    "Carrera técnica/comercial", 
                                    "Profesional (universidad)", 
                                    "Posgrado", 
                                    "Ninguno", 
                                    "No sabe", 
                                    "Sin responder"
                                ]}
                                data={[
                                    this.state.escolaridad._1_, 
                                    this.state.escolaridad._2_, 
                                    this.state.escolaridad._3_, 
                                    this.state.escolaridad._4_, 
                                    this.state.escolaridad._5_, 
                                    this.state.escolaridad._6_, 
                                    this.state.escolaridad._7_, 
                                    this.state.escolaridad._8_, 
                                    this.state.escolaridad._8555_, 
                                    this.state.escolaridad._8666_, 
                                    this.state.escolaridad.nan
                                ]} 
                            />
                        </Element>
                        : <Element md={12} />
                    }
                </Row>

                 {/* Graficas de Dedicacion/Programa socials */}
                 <Row className="box-element-container">
                    { this.state.cargarDedicacion ?
                        <Element title="Principal actividad" md={12} fill={1}>
                            <ChartHorizontalBar
                                color={1}
                                label="Registros"
                                labels={["Ama de casa", "Trabajadora/Empleada", "Estudiante", "Otro", "Sin responder"]} 
                                data={[this.state.dedicacion._1_, this.state.dedicacion._2_, this.state.dedicacion._3_, this.state.dedicacion._1111_, this.state.dedicacion.nan]}
                            />
                        </Element>
                        : <Element md={12} />
                    }
                    { this.state.cargarProgramaSocial ?
                        <Element title="Pertenencia a programa social" md={12} fill={1}>
                            <ChartPie 
                                labels={[
                                    "Si", "No", "No sabe", "Sin responder"
                                ]}
                                data={[
                                    this.state.programaSocial._1_, this.state.programaSocial._2_, this.state.programaSocial._8666_, this.state.programaSocial.nan
                                ]} 
                            />
                        </Element>
                        : <Element md={12} />
                    }
                </Row>
            </Grid>
        );
    }
}

export default P1Madres;